* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.Home-container {
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;
}

.Home-Header {
	padding: 10px;
}

.Home-scroll {
	flex-grow: 1;
	display: flex;
	align-items: center;
	justify-content: center;
	/* overflow-x: auto !important; */
}

.block {
	/* width: 580px; */
	width: 200px;
	height: 460px;
	cursor: pointer;
}

.block-1 {
	margin: 10px;
	background-image: url(../pictures/Citrus.jpg);
	background-size: cover;
	display: flex;
	align-items: flex-end;
	z-index: 999;
}

.block-1:hover {
	transform: scale(1.5);
	z-index: 999;
}

.block-2 {
	margin: 10px;
	background-image: url(../pictures/Supreme.jpg);
	background-size: cover;
	display: flex;
	align-items: flex-start;
}

.block-2:hover {
	transform: scale(1.5);
	z-index: 999;
}

.block-3 {
	margin: 10px;
	background-image: url(../pictures/Watson.jpg);
	background-size: cover;
	display: flex;
