}

.block-5:hover {
	transform: scale(1.5);
	z-index: 999;
}
.Home-socials {
	margin: 10px;
}

.Home-socials > span {
	padding-right: 15px;
	/* font-family: 'Monument Extended';
	font-weight: lighter; */
	font-size: 18px;
	text-decoration: none;
	color: white;
}

a {
	text-decoration: none;
	color: white;
}

h1 {
	/* font-family: 'Monument Extended'; */
	font-weight: lighter;
	font-size: 64px;
	padding: 10px;
	writing-mode: vertical-rl;
	text-orientation: mixed;
	/* transform: rotate(-45deg); */
}

h2 {
	/* font-family: 'Monument Extended';
	font-weight: lighter; */
	font-size: 30px;
}

@media only screen and (max-width: 425px) {
	h1 {
		padding: 0;
		font-size: 50px;
	}
}
